import React, { useEffect, useState } from "react";
import { authService } from "fbase";
import { useNavigate } from "react-router-dom";
import { db } from "fbase";
import {
  collection,
  getDocs,
  orderBy,
  query,
  where,
} from "@firebase/firestore";

import { updateProfile } from "@firebase/auth";
import Nweet from "components/Nweet";
const Profile = ({ userObj, refreshUser, setInit }) => {
  const [arr, setArr] = useState([]);
  const [newName, setNewName] = useState(userObj.displayName);

  const navigate = useNavigate();
  const getData = async () => {
    try {
      const getCol = await getDocs(
        query(
          collection(db, "nweets"),
          orderBy("createdAt", "desc"),
          where("creatorId", "==", userObj.uid)
        )
      );
      const newArr = getCol.docs.map((doc) => doc.data());

      setArr(newArr);
    } catch (e) {
      console.log(e);
    }
  };

  useEffect(() => {
    getData();
  }, [userObj]);

  const onLogOutClick = () => {
    authService.signOut();
    setInit(false);
    navigate("/");
  };
  const onSubmit = async (e) => {
    e.preventDefault();
    if (newName === "") {
    }
    if (newName !== userObj.displayName) {
      await updateProfile(userObj, { displayName: newName });
      refreshUser();
    }
  };
  const onChange = (e) => {
    const value = e.target.value;
    setNewName(value);
  };
  return (
    <div className="container">
      <form onSubmit={onSubmit} className="profileForm">
        <input
          type="string"
          onChange={onChange}
          autoFocus
          placeholder="new name"
          value={newName || ""}
          className="formInput"
        />
        <input
          type="submit"
          value="Update Profile"
          className="formBtn"
          style={{
            marginTop: 10,
          }}
        />
      </form>
      <span className="formBtn cancelBtn logOut" onClick={onLogOutClick}>
        Log Out
      </span>

      {arr.map((nweetObj, idx) => (
        <Nweet key={idx} nweetObj={nweetObj} userObj={userObj} />
      ))}
    </div>
  );
};
export default Profile;
