import React, { useState } from "react";
import { doc, deleteDoc, updateDoc } from "firebase/firestore";
import { db } from "fbase";
import { getStorage, deleteObject, ref } from "@firebase/storage";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash, faPencilAlt } from "@fortawesome/free-solid-svg-icons";
const Nweet = ({ nweetObj, userObj }) => {
  const isOwner = userObj.uid === nweetObj.creatorId;
  const [editing, setEditing] = useState(false);
  const [value, setValue] = useState(nweetObj.text);
  const onDelete = async () => {
    const ok = window.confirm("you really wanna delete this?");
    if (ok) {
      if (nweetObj.attachment) {
        console.log(nweetObj.attachment);
        const storage = getStorage();
        await deleteObject(ref(storage, nweetObj.attachment));
      }
      await deleteDoc(doc(db, "nweets", nweetObj.id));
    }
  };
  const toggleEditing = () => setEditing((prev) => !prev);
  const onChange = (e) => {
    const val = e.target.value;
    setValue(val);
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    const changeDoc = doc(db, "nweets", nweetObj.id);
    await updateDoc(changeDoc, {
      text: value,
    });
    setEditing(false);
  };

  return (
    <div className="nweet">
      {editing ? (
        <>
          <form onSubmit={onSubmit} className="container nweetEdit">
            <input
              type="text"
              placeholder="Edit your nweet"
              value={value}
              required
              autoFocus
              onChange={onChange}
              className="formInput"
            />
            <input type="submit" value="Update Nweet" className="formBtn" />
          </form>
          <span onClick={toggleEditing} className="formBtn cancelBtn">
            Cancel
          </span>
        </>
      ) : (
        <>
          <h4>
            {nweetObj.creator
              ? `${nweetObj.text} - ${nweetObj.creator}님`
              : `${nweetObj.text} - 익명`}
          </h4>
          {nweetObj.attachment && <img src={nweetObj.attachment} />}
          {isOwner && (
            <div className="nweet__actions">
              <span onClick={onDelete}>
                <FontAwesomeIcon icon={faTrash} />
              </span>
              <span onClick={toggleEditing}>
                <FontAwesomeIcon icon={faPencilAlt} />
              </span>
            </div>
          )}
        </>
      )}
    </div>
  );
};
export default Nweet;
